/******************************************************************
	GENERAL
******************************************************************/

.container {
    position: relative;
}

.container__inner {
    overflow: hidden;
  	margin-right: auto;
  	margin-left: auto;
  	width: 95%;

  	@include small {
        width: 90%;
  	}

  	@include medium {
  		  width: 80%;
  	}

  	@include large {
    		max-width: 1400px;
    		width: 70%;
  	}
}

.col {
  	float: left;
  	position: relative;
  	min-height: 1px;
  	padding-left: $g-vgap;
    padding-right: $g-vgap;
  	margin-top: $g-hgap;

    &--no-vgap {
        padding-left: 0px;
        padding-right: 0px;
    }

    &--no-hgap {
        margin-top: 0px;
    }
}

.row {
	margin-left: -$g-vgap;
    margin-right: -$g-vgap;

    &--no-vgap {
        margin-left: 0;
        margin-right: 0;
    }

    &--no-hgap {
        margin-top: $g-hgap;
    }

    &--first {
        margin-top: -$g-hgap;
    }
}

.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after {
  content: " ";
  display: table;
}

.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}



